<template>
  <div>
    <ul class="manage_list_common setting_list">
      <li class="upload">
        <p></p>
        <p><img :src="headImage">
          <input type="file" id="file" @change="uploadImageChange"></p>

        <p><span @click="sendUploadHeadImage"></span></p>
      </li>
      <li>
        <p></p>
        <p>123123231123@qq.com</p>
        <p>
          <span @click="email_edit = true"></span>
        </p>
      </li>
      <li>
        <p></p>
        <p>183****1234</p>
        <p>
          <span @click="tel_edit = true"></span>
        </p>
      </li>
      <li>
        <p></p>
        <p>********</p>
        <p>
          <span @click="pass_edit = true"></span>
        </p>
      </li>
    </ul>
    <div class="windows_wrapper" v-show="email_edit">
      <div class="windows_box">
        <div class="windows_head">
          <span></span>
          <span class="icon-close" @click="email_edit = false"></span>
        </div>
        <div class="windows_content">
          <section>
            <span class="label"></span>
            <input v-model="input_email" type="text" placeholder="">
          </section>
        </div>
        <div class="windows_btn">
          <span class="windows_btn_cancel" @click="email_edit = false"></span>
          <span class="windows_btn_confirm" @click="dealBindEmail"></span>
        </div>
      </div>
    </div>
    <div class="windows_wrapper" v-show="tel_edit">
      <div class="windows_box">
        <div class="windows_head">
          <span></span>
          <span class="icon-close" @click="tel_edit = false"></span>
        </div>
        <div class="windows_content">
          <section>
            <span class="label"></span>
            <input v-model="input_old_phone" type="text" placeholder="" class="shot">
            <span class="smscode_btn" @click="dealSendOldPhoneCode"></span>
          </section>
          <section>
            <span class="label"></span>
            <input v-model="input_old_code" type="text" placeholder="">
          </section>
          <section>
            <span class="label"></span>
            <input v-model="input_new_phone" type="text" placeholder="" class="shot">
            <span class="smscode_btn" @click="dealSendNewPhoneCode"></span>
          </section>
          <section>
            <span class="label"></span>
            <input v-model="input_new_code" type="text" placeholder="">
          </section>
        </div>
        <div class="windows_btn">
          <span class="windows_btn_cancel" @click="tel_edit = false"></span>
          <span class="windows_btn_confirm" @click="dealChangePhone"></span>
        </div>
      </div>
    </div>
    <div class="windows_wrapper" v-show="pass_edit">
      <div class="windows_box">
        <div class="windows_head">
          <span></span>
          <span class="icon-close" @click="pass_edit = false"></span>
        </div>
        <div class="windows_content">
          <section>
            <span class="label"></span>
            <input v-model="input_old_password" type="text" placeholder="">
          </section>
          <section>
            <span class="label"></span>
            <input v-model="input_new_password" type="text" placeholder="">
          </section>
          <section>
            <span class="label"></span>
            <input v-model="input_new_sure_password" type="text" placeholder="">
          </section>
        </div>
        <div class="windows_btn">
          <span class="windows_btn_cancel" @click="pass_edit = false"></span>
          <span class="windows_btn_confirm" @click="dealChangePassword"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data(){
      return{
        email_edit: false,
        tel_edit: false,
        pass_edit: false,
        headImage:require('./images/uploadimg.png'),
        file:"",

        input_email:"",

        input_old_phone:"",
        input_old_code:"",
        input_new_phone:"",
        input_new_code:"",

        input_old_password:"",
        input_new_password:"",
        input_new_sure_password:""
      }
    },
    created(){
      loginStatus(this);
      this.headImage = path + this.img;
    },
    methods:{
      dealChangePassword:function(){

        if(this.input_old_password == ""){
          alert("");
          return;
        }

        if(this.input_new_password == ""){
          alert("");
          return;
        }

        if(this.input_new_sure_password == ""){
          alert("");
          return;
        }


        var url = path + "/index/user/change-user-pwd";
        var dict = {
          user_id:this.userId,
          pwd:this.input_new_password,
          pwd_sure:this.input_new_sure_password,
          old_pwd:this.input_old_password
        }
        this.$http.post(url,dict,{"emulateJSON":true}).then(function(r){
          var td = r.data;
          if(td.state == 1){
            alert("")
          }else{
            alert("")
          }
        })

      },
      dealSendOldPhoneCode:function(){

      },
      dealSendNewPhoneCode:function(){

      },
      dealChangePhone:function(){

        if(this.input_old_phone == ""){
          alert("");
          return;
        }

        if(this.input_old_code == ""){
          alert("");
          return;
        }

        if(this.input_new_phone == ""){
          alert("");
          return;
        }

        if(this.input_new_code == ""){
          alert("");
          return;
        }

        var url = path + "/index/login/change-user-phone";
        var dict = {
          user_id:this.userId,
          old_phone:this.input_old_phone,
          phone:this.input_new_phone,
          code:this.input_new_code
        }
        this.$http.post(url,dict,{"emulateJSON":true}).then(function(r){
          var td = r.data;
          if(td.state == 1){
            alert("")


          }else{
            alert("")
          }
        })


      },
      dealBindEmail:function(){

        if(this.input_email == ""){
          alert("");
          return;
        }

        var url = path + "/index/user/bind-user-email";
        var dict = {
          user_id:this.userId,
          email:this.input_email
        }
        this.$http.post(url,dict,{"emulateJSON":true}).then(function(r){
          var td = r.data;
          if(td.state == 1){
            alert("")
          }else{
            alert("")
          }
        })

      },
      sendUploadHeadImage:function(){


        if(this.file == ""){
          alert("");
          return;
        }

        //
        var url = path + "/api/fileUpload/change-user-img";

        //formData
        var formData = new FormData();
        

        formData.append("user_id",this.userId);
        formData.append("file",this.file);
        console.log(this.file)

        
        //
        this.$http.post(url,formData,function(){
          console.log("send")
        }).then(function (r) {
          //console.log(JSON.stringify(r.data))
          var td = r.data;
          if(td.state == 1){

            if(localStorage.img){
              localStorage.img = td.img;
            }else if(sessionStorage.img){
              sessionStorage.img = td.img;
            }

            alert(r.data.info+" ");
            // location.reload();

          }else{
            alert(r.data.info);
          }
        })


      },
      uploadImageChange:function(){
        //
        var fileTag = document.getElementById("file");
        
        
        //
        if(fileTag.files.length==0){
          return ;
        }
        var uploadFile = fileTag.files[0];
        var url = window.URL.createObjectURL(uploadFile);
        console.log("url = "+url);
        
        
        this.file = uploadFile;

        //
        this.headImage = url;
      }
    }
  }
</script>

<style media="screen">
  .upload img{
    width: 100px;
    height: 100px;
  }
</style>



