        invoice_header:"2",
        registration_no:"3",
        bank_name:"4",
        bank_num:"5",
        adress:"6",
        mobile:"7",
        shuiwu_img:require("../../assets/uploadimg.png"),
        saomiao_img:require("../../assets/uploadimg.png"),
        address_id:"10",

