<template>
  <div class="tab_type_box">
    <div class="tab_type_head">
      <a class="active">{{typeName}}</a>
    </div>
    <div class="padding20">
      <ul class="manage_list_common setting_list">
        <li class="name-filed">
          <p>{{typeName}}</p>
          <input type="text" :placeholder="''+typeName+''" v-model="company_name" disabled="" />
        </li>
        <li>
          <p></p>
          <input type="text" placeholder="" v-model="realName">
        </li>
        <li>
          <p></p>
          <input type="text" placeholder="" v-model="phone">
        </li>
        <li>
          <p></p>
          <input type="text" placeholder="" v-model="email">
        </li>
        <li class="upload">
          <p></p>
          <div class="upload-box">
            <img :src="uploadImgUrl" />
            <input id="file" class="file-upload" type="file" @change="dealImgChange"/>
          </div>
        </li>
        <li>
          <p></p>
          <span class="submit_btn company_submit" @click="dealSendClaim"></span>
          <span class="cancel_btn company_cancel"></span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data(){
      return {
        type:"company",
        typeName:"",
        company_name:"",
        uploadImgUrl: require("../../assets/uploadimg.png"),

        realName:"",
        phone:"",
        email:"",
        file:""

      }
    },
    created(){

      loginStatus(this)


      var type = this.$route.query.type;
      if(type == "organization"){
        this.typeName = ""
        this.type = "organization"

      }

      this.company_name = this.$route.query.name;
    },
    methods:{
      dealImgChange:function(){
        //
        var fileTag = document.getElementById("file");
        
        
        //
        if(fileTag.files.length==0){
          return ;
        }
        var uploadFile = fileTag.files[0];
        var url = window.URL.createObjectURL(uploadFile);
        console.log("url = "+url);
        
        
        this.file = uploadFile;

        //
        this.uploadImgUrl = url;
      },
      dealSendClaim:function(){

        //
        if(this.realName.length == 0){
          alert("");
          return;
        }

        if(this.phone.length == 0){
          alert("");
          return;
        }

        if(this.email.length == 0){
          alert("email");
          return;
        }

        if(this.file == ""){
          alert("");
          return;
        }


        //: 
        var id = this.$route.query.id;
        console.log("id = "+id);

        var url = null;
        var formData = null;
        if(this.type == "company"){

          //
          url = path + "/index/enterprise/enterprise-claim";

          //formData
          formData = new FormData();
          
          formData.append("enterprise_id",id);
          formData.append("user_name",this.realName);
          formData.append("user_id",this.userId);
          formData.append("phone",this.phone);
          formData.append("email",this.email);

          formData.append("license_path",this.file);
        }
        else if(this.type == "organization"){
          //
          url = path + "/index/company/company-claim";

          //formData
          formData = new FormData();
          
          formData.append("company_id",id);
          formData.append("user_name",this.realName);
          formData.append("user_id",this.userId);
          formData.append("phone",this.phone);
          formData.append("email",this.email);

          formData.append("license_path",this.file);
        }

        //
        this.$http.post(url,formData,function(){
          console.log("send")
        }).then(function (r) {
          //console.log(JSON.stringify(r.data))
          if(r.data.status == 1){
            alert(r.data.info);
          }else{
            alert(r.data.info);
          }
        })
        

      }
    }
  }
</script>

<style media="screen">
  .company_submit,.company_cancel{
    width: 160px;
    margin: 0 10px;
  }
  .name-filed input{
    color: #aaa;
  }
  .upload-box{
    position: relative;
    overflow: hidden;
  }
  .file-upload{
    position: absolute;
    top:0;
    opacity: 0;
    padding-bottom: 56px;
  }
</style>



