<template>
  <div class="tab_type_box">
    <div class="tab_type_head">
      <a class="active"></a>
    </div>
    <div class="padding20">
      <ul class="messageList">
        <li v-for="item in messageList">
          <section>
            <p>
              <span class="messageTitle">{{item.message_type_string}}</span>
              <span class="messageTime">{{item.created_at}}</span>
            </p>
            <p class="messageInfor">{{item.content}}</p>
          </section>
          <section>
            <span class="messageBtn messageBtnDetail" @click="dealShowMessage(item)"></span>
            <span class="messageBtn messageBtnDelet" @click="dealDelMessage(item.id)"></span>
          </section>
        </li>
        <!-- <li>
          <section>
            <p>
              <span class="messageTitle"></span>
              <span class="messageTime">2017-05-01 12:20:30</span>
            </p>
            <p class="messageInfor"></p>
          </section>
          <section>
            <span class="messageBtn messageBtnDetail" @click="message_info = true"></span>
            <span class="messageBtn messageBtnDelet"></span>
          </section>
        </li> -->
      </ul>
      <ZHPagination />
    </div>
    <div class="windows_wrapper" v-show="message_info">
        <div class="windows_box">
            <div class="windows_head">
                <span></span> 
                <span class="icon-close" @click="message_info = false"></span>
            </div> 
            <div class="windows_content">
                <section>
                    <span class="label"></span> 
                    <div></div>
                </section>
                <section>
                    <span class="label"></span> 
                    <div>2017-12-29 15:20:10</div>
                </section>
                <section>
                    <span class="label"></span> 
                    <div class="windows-cont">4101402611796.5</div>
                </section>
            </div> 
            <div class="windows_btn">
                <span class="windows_btn_cancel windows_btn_del"></span> 
                <span class="windows_btn_confirm" @click="message_info = false"></span>
            </div>
        </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">

  import ZHPagination from '../ZHPagination/ZHPagination'

  export default {
    components:{
      ZHPagination
    },
    data(){
      return {
        messageList:[],
        message_info:false
      }
    },
    created(){
      loginStatus(this);

      this.downloadList();
    },
    methods:{
      downloadList:function(){
        var url = path + "/index/user/message?user_id="+this.userId;
        this.$http.get(url).then(function(r){
          var td = r.data;
          this.messageList = td.data;
        })
      },
      dealDelMessage:function(id){
        var url = path + "/index/user/del-message?message_id="+id;
        this.$http.get(url).then(function(r){
          var td = r.data;
          //console.log("td="+JSON.stringify(td))
          if(td.state == 1){
            alert("");

            this.downloadList();

          }else{
            alert("!");
          }
        })
      },

      dealShowMessage:function(item){
        this.message_info = true
        //alert(":"+item.created_at+" :"+item.message_type_string+" :"+item.content);
      }
    }

  }
</script>

<style media="screen">
.messageList li{
  display: flex;
  width: 880px;
  height: 90px;
  padding: 0 20px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  border: 1px solid #eef0f6;
  background: #f9fafc;
}
.messageList li section:nth-child(1){
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.messageList li section:nth-child(2){
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.messageTitle{
  font-size: 14px;
  font-weight: bold;
  color: #616161;
}
.messageTime{
  font-size: 13px;
  color: #b3b3b3;
}
.messageInfor{
  font-size: 14px;
  color: #898989;
  line-height: 30px;
}
.messageBtn{
  padding: 5px 20px;
  border-radius: 3px;
  background: #fff;
    cursor: pointer;
    transition: .3s
}
.messageBtnDetail{
  border: 1px solid #72CE6F;
  color: #72CE6F;
}
    .messageBtnDetail:hover{
        background: #72CE6F;
        color: #fff
    }
.messageBtnDelet{
  border: 1px solid #E5E7EE;
  color: #898989;
  margin-left: 20px;
    transition: .3s;
}
    .messageBtnDelet:hover{
        color: #FF826B
    }
    .windows-cont{
        width: 550px
    }
    .windows_content>*{
        height: auto!important;
        padding: 6px 0;
        align-items:baseline!important;
        line-height: 20px
    }
    .windows_btn{
        height: auto;
        padding-bottom: 30px
    }
    .windows_btn_del{
        color: #FF826B
    }
</style>



