<template>
  <div>
    <invoiceListCommon :list="items"></invoiceListCommon>
    <moPaging 
            :page-index="currentPage" 
            :total="count" 
            :page-size="pageSize" 
            @change="pageChange">
            </moPaging>
  </div>
</template>

<script type="text/ecmascript-6">
import invoiceListCommon from "../invoiceListCommon/invoiceListCommon";
import moPaging from "../MoPaging/MoPaging";
export default {
  components: {
    invoiceListCommon,
    moPaging
  },
  data() {
    return {
      pageSize: 15, //20
      currentPage: 1, //
      count: 0, //
      items: []
    };
  },
  created() {
    loginStatus(this);
    this.getList();
  },
  methods: {
    
    //
    getList() {
     var url = path + "/index/invoice?user_id=" + this.userId + "&page=" + this.currentPage + "&status=2";
      this.$http.get(url).then(function(r) {
        var td = r.data;
        this.items = td.data;
      });
    },

    //pagepage
    pageChange(page) {
      this.currentPage = page;
      this.getList();
    }
  }
};
</script>

<style media="screen">

</style>



