<template>
  <div>
    <ul class="manage_list_common withdraw_list">
      <li>
        <p></p>
        <input v-model="fromdata.money" type="text" placeholder="">
      </li>
      <li>
        <p></p>
        <select v-model="fromdata.withdrawals_type">
          <option value="1"></option>
          <option value="2"></option>
          <option value="3"></option>
        </select>
      </li>
      <li>
        <p></p>
        <input v-model="fromdata.account_name" type="text" placeholder="">
      </li>
      <li>
        <p></p>
        <input v-model="fromdata.account_num" type="text" placeholder="">
      </li>
      <li>
        <p></p>
        <span class="submit_btn" @click="withDraw"></span>
        <router-link :to="{name:'wallet'}" class="cancel_btn"></router-link>
      </li>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data(){
      return{
        tabTitle: '',
        url:path+'/index/wallet/apply-withdrawals',
        fromdata:{
          user_id: '',
          money:'',
          withdrawals_type:1,
          account_name:'',
          account_num:'',
        }
      }
    },
    created(){
      loginStatus(this);
      this.fromdata.user_id = this.userId;
    },
    mounted(){
      this.$emit('post-title',this.tabTitle);
    },
    methods:{
      withDraw:function(){
        if (!this.fromdata.money) {
          return false;
        }
        if (!this.fromdata.withdrawals_type) {
          return false;
        }
        if (!this.fromdata.account_name) {
          return false;
        }
        if (!this.fromdata.account_num) {
          return false;
        }
        this.$http.post(this.url,this.fromdata,{"emulateJSON":true}).then(function(r){
          var data = r.data;
          if (data.status == 1) {
            alert(data.info)
          } else {
            alert(data.info)
          }
        })
      }
    }
  }
</script>

<style media="screen">
  .withdraw_list .submit_btn,.withdraw_list .cancel_btn{
    width: 158px;
    margin-right: 10px;
  }
  .withdraw_list li:nth-last-child(2){
    border-bottom: 0;
  }
</style>



